import axios from './axios'

//
export const Login = params => {
    return  axios({
        url: 'user/login',
        method: 'post',
        data: params
    });
};

//
export const ModifyPassword = params => {
    return axios({
        url: 'user/modifyPassword',
        method: 'post',
        data: params
    });
};

//
export const getProjectList = params => {
    return  axios({
        url: 'item/findList',
        method: 'post',
        data: params,
        headers: {
            "Content-Type": "application/json;charset=utf-8"
        },
    });
};

//logo url
export const getLogoUrl = params => {
    return  axios({
        url: 'item/upload',
        method: 'post',
        data: params,
        headers: {
            "Content-Type": "application/json;charset=utf-8"
        },
    });
};

//id
export const findOne = params => {
    return  axios({
        url: 'item/findOne',
        method: 'get',
        params,
    });
};

//
export const updateProject = params => {
    return  axios({
        url: 'item/update',
        method: 'post',
        data: params,
        headers: {
            "Content-Type": "application/json;charset=utf-8"
        },
    });
};

//
export const addProject = params => {
    return  axios({
        url: 'item/add',
        method: 'post',
        data: params,
    });
};

//UV
export const UVProject = () => {
    return  axios({
        url: 'UV/find',
        method: 'post',
    });
};