<template>
    <section class="main_cont">
        <!--  -->
        <el-row>
            <el-table class="project_list" :data="uv_info" border highlight-current-row v-loading="listLoading" height="800">
                <el-table-column type="selection" width="55" align="center"></el-table-column>
                <el-table-column prop="id" label="id" width="60" align="center"></el-table-column>
                <el-table-column prop="title" label="" width="200" align="center"></el-table-column>
                <el-table-column label="logo" width="200" align="center">
      	<template slot-scope="scope">
      		<img :src="scope.row.logo" class="tabimg" />
      	</template>
                </el-table-column>
                <el-table-column prop="countUV" label="" width="auto"></el-table-column>
            </el-table>

            <!--  -->
            <el-col :span="24" class="toolbar">
                <el-pagination layout="prev, pager, next, total"  @current-change="handleCurrentChange" @size-change="handleSizeChange" :total="page_arg.total" style="float:right;">
                </el-pagination>
            </el-col>
        </el-row>
    </section>
</template>

<script>
    import {
        UVProject  //UV
    } from '../api/api.js';

    export default {
        name: "uv",

        data () {
            return{
                /**
                 * common
                 */
                listLoading: false,  //lodding
                dialogVisible: false,  //

                //uv
                uv_info: [],

                //
                page_arg: {
                    pagesize: "10",  // 
                    currentPage: "1",  // 
                    total: 0,  // table :total
                },
            }
        },
        methods: {
            /**
             * common
             */
            //
            handleClose(done) {
                this.$confirm('').then(() => {
                    done();
                }).catch(err => {
                    throw err;
                });
            },

            /**
             *  
             */
            // --
            handleCurrentChange(val) {
                this.page_arg.currentPage = val;
                this.getProjectList();
            },
            // --
            handleSizeChange (size) {
                this.page_arg.pagesize = size;
                this.getProjectList();
            },

            /**
             * api
             */
            //
            getProjectUV () {
                UVProject().then(res => {
                    console.log(res.data.data);
                    this.uv_info = res.data.data;
                });
            },
        },
        created () {
            this.getProjectUV();
        }
    }
</script>

<style scoped>
    .tabimg{
        display: block;
        width: 40px;
        height: 40px;
        margin: 0 auto;
    }
</style>