<template>
    <div class="register">
        <section class="form_container">
            <div class="manage_tip">
                <span class="title"></span>
            </div>
            <!-- ref -->
            <el-form :model="registerUser" @keyup.enter.native="submitForm('registerForm')" status-icon :rules="rules" class="registerForm" ref="registerForm" label-width="80px">
                <el-form-item label="" prop="name">
                    <el-input v-model="registerUser.name" placeholder=""></el-input>
                </el-form-item>
                <el-form-item label="" prop="password">
                    <el-input v-model="registerUser.password" placeholder="" type="password"></el-input>
                </el-form-item>
                <el-form-item label="" prop="password2">
                    <el-input v-model="registerUser.password2" placeholder="" type="password"></el-input>
                </el-form-item>
                <el-form-item class="btn_wrap">
                    <el-button type="primary" class="submit_btn" @click="submitForm('registerForm')" @keyup.enter.native="submitForm('registerForm')"> </el-button>
                </el-form-item>
            </el-form>
        </section>
    </div>
</template>

<script>
    export default {
        name: "register",

        data() {
            //
            const validatePass2 = (rule, value, callback) => {
                if (value !== this.registerUser.password) {
                    callback(new Error("!"));
                } else {
                    callback();
                }
            };
            return {
                registerUser: {
                    name: "",
                    password: "",
                    password2: ""
                },
                rules: {
                    name: [
                        { required: true, message: "", trigger: "blur" },
                        { min: 2, max: 30, message: " 2  30 ", trigger: "blur" }
                    ],
                    password: [
                        { required: true, message: "", trigger: "blur" },
                        { min: 6, max: 30, message: " 6  30 ", trigger: "blur" }
                    ],
                    password2: [
                        { required: true, message: "", trigger: "blur" },
                        { min: 6, max: 30, message: " 6  30 ", trigger: "blur" },
                        //
                        { validator: validatePass2, trigger: "blur" }
                    ]
                }
            };
        },
        methods: {
            submitForm(formName) {
                //
                this.$refs[formName].validate(valid => {
                    //
                    if (valid) {
                        this.$axios
                            .get("/api/form", this.registerUser)
                            .then(res => {
                                console.log(res);

                                // 
                                this.$message({
                                    message: "",
                                    type: "success"
                                });
                                this.$router.push("/login");
                            });
                    } else {
                        console.log("error submit!!");
                        return false;
                    }
                });
            }
        }
    };
</script>


<style scoped>
    .register {
        position: absolute;
        width: 100%;
        height: 100%;
        background: url(../assets/images/bg.jpg) no-repeat center center;
        background-size: 100% 100%;
    }

    .form_container {
        width: 370px;
        height: 210px;
        position: absolute;
        top: 10%;
        left: 34%;
        padding: 25px;
        border-radius: 5px;
        text-align: center;
    }

    .form_container .manage_tip .title {
        font-family: "Microsoft YaHei";
        font-weight: bold;
        font-size: 26px;
        color: #fff;
    }

    .registerForm {
        margin-top: 20px;
        background-color: #fff;
        padding: 20px 40px 20px 20px;
        border-radius: 5px;
        box-shadow: 0px 5px 10px #cccc;
    }

    .submit_btn {
        width: 100%;
    }
</style>