<template>
    <section class="main_cont">
        <!--  -->
        <el-row>
            <el-form :inline="true">
                <el-col class="toolbar bdr_radiu" :span="24">
                    <el-col :span="22">
                        <el-form-item label="">
                            <el-input placeholder="" v-model="search_list_arg.title" @keyup.enter.native="findProjectList" clearable></el-input>
                        </el-form-item>
                        <el-form-item label="">
                            <el-input placeholder="" v-model="search_list_arg.type" @keyup.enter.native="findProjectList" clearable></el-input>
                        </el-form-item>
                        <el-form-item label="">
                            <el-input placeholder="" v-model="search_list_arg.desc" @keyup.enter.native="findProjectList" clearable></el-input>
                        </el-form-item>
                        <el-form-item>
                            <el-button type="primary" @click="findProjectList"></el-button>
                        </el-form-item>
                        <el-form-item>
                            <el-button type="primary" v-on:click="addProjectVisible = true"></el-button>
                        </el-form-item>
                    </el-col>
                    <el-col class="tip">(0-2){[0:][1:][2:]}</el-col>
                    <!--<el-col :span="22">-->
                        <!--<el-form-item label="id">-->
                            <!--<el-input placeholder="id" @keyup.enter.native="findProject" v-model="project_id" clearable></el-input>-->
                        <!--</el-form-item>-->
                        <!--<el-form-item>-->
                            <!--<el-button type="primary" @click="findProject"></el-button>-->
                        <!--</el-form-item>-->
                        <!--<el-form-item>-->
                            <!--<el-button type="primary" @click="addProject" v-on:click="addProjectVisible = true"></el-button>-->
                        <!--</el-form-item>-->
                    <!--</el-col>-->
                </el-col>
            </el-form>
        </el-row>

        <!--  -->
        <el-row>
            <el-table class="project_list" :data="project_info" border highlight-current-row v-loading="listLoading" height="720">
                <el-table-column type="selection" width="55" align="center"></el-table-column>
                <!--<el-table-column type="index" width="60" align="center"></el-table-column>-->
                <el-table-column prop="id" label="id" width="60" align="center"></el-table-column>
                <el-table-column prop="title" label="" width="150" align="center"></el-table-column>
                <el-table-column label="logo" width="150" align="center">
      	<template slot-scope="scope">
      		<img :src="scope.row.logoUrl" class="tabimg" />
      	</template>
                </el-table-column>
                <el-table-column prop="amount" label="" width="80" align="center"></el-table-column>
                <el-table-column prop="type" label="" width="80" align="center"></el-table-column>
                <el-table-column prop="desc" label="" width="auto"></el-table-column>

                <el-table-column fixed="right" label="" width="100">
                    <template slot-scope="scope">
                        <el-button @click="updateProject(scope.row)" v-on:click="updateProjectVisible = true" type="text" size="small"></el-button>
                    </template>
                </el-table-column>
            </el-table>

            <!--  -->
            <el-col :span="24" class="toolbar">
                <el-pagination layout="prev, pager, next, total"  @current-change="handleCurrentChange" @size-change="handleSizeChange" :total="page_arg.total" style="float:right;">
                </el-pagination>
            </el-col>
        </el-row>

        <!--  -->
        <el-dialog title="" @keyup.enter.native="addProjectSubmit('addProjectForm')" :close-on-click-modal="false" :visible.sync="addProjectVisible" :before-close="handleClose">
            <el-col class="tip mgt toolbar">(0-2){[0:][1:][2:]}</el-col>

            <el-form :model="addProjectData" status-icon :rules="addProjectRules" ref="addProjectForm" label-width="130px">
                <el-form-item label="" prop="title">
                    <el-input v-model="addProjectData.title" placeholder="" clearable></el-input>
                </el-form-item>
                <el-form-item label="logo">
                    <el-upload
                            class="upload-demo"
                            action="http://10.10.10.238:8090/admin/item/upload"
                            ref='upload'
                            :before-remove="beforeRemove"
                            :on-change="addHandleChange"
                            multiple
                            :limit="upload_arg.limit"
                            :on-exceed="handleExceed"
                            :file-list="upload_arg.fileList">
                        <el-button size="small" type="primary"></el-button>
                        <!--<div slot="tip" class="el-upload__tip"></div>-->
                    </el-upload>
                </el-form-item>
                <el-form-item label="logo" prop="title">
                    <el-input v-model="addProjectData.logoUrl" placeholder="logo" clearable></el-input>
                </el-form-item>
                <el-form-item label="" prop="amount">
                    <el-input v-model="addProjectData.amount" placeholder="" clearable></el-input>
                </el-form-item>
                <el-form-item label="" prop="type">
                    <el-input v-model="addProjectData.type" placeholder="" clearable></el-input>
                </el-form-item>
                <el-form-item label="" prop="desc">
                    <el-input v-model="addProjectData.desc" placeholder="desc" clearable></el-input>
                </el-form-item>
                <el-form-item>
                    <el-button type="primary" @click="addProjectSubmit('addProjectForm')"></el-button>
                    <el-button @click="resetForm('addProjectForm')"></el-button>
                </el-form-item>
            </el-form>
        </el-dialog>

        <!--  -->
        <el-dialog title="" :close-on-click-modal="false" @keyup.enter.native="updateProjectSubmit('updateProjectForm')" :visible.sync="updateProjectVisible" :before-close="handleClose">
            <el-col class="tip mgt toolbar">(0-2){[0:][1:][2:]}</el-col>

            <el-form :model="updateProjectData" status-icon :rules="updateProjectRules" ref="updateProjectForm" label-width="130px">
                <el-form-item label="id" prop="id">
                    <el-input v-model="updateProjectData.id" disabled></el-input>
                </el-form-item>
                <el-form-item label="" prop="title">
                    <el-input v-model="updateProjectData.title" placeholder="" clearable></el-input>
                </el-form-item>
                <el-form-item label="logo">
                    <el-upload
                            class="upload-demo"
                            action="http://10.10.10.238:8090/admin/item/upload"
                            ref='upload'
                            :before-remove="beforeRemove"
                            :on-change="updateHandleChange"
                            multiple
                            :limit="upload_arg.limit"
                            :on-exceed="handleExceed"
                            :file-list="upload_arg.fileList">
                        <el-button size="small" type="primary"></el-button>
                        <!--<div slot="tip" class="el-upload__tip"></div>-->
                    </el-upload>
                </el-form-item>
                <el-form-item label="logo" prop="logoUrl">
                    <el-input v-model="updateProjectData.logoUrl" placeholder="logo" clearable></el-input>
                </el-form-item>
                <el-form-item label="" prop="amount">
                    <el-input v-model="updateProjectData.amount" placeholder="" clearable></el-input>
                </el-form-item>
                <el-form-item label="" prop="type">
                    <el-input v-model="updateProjectData.type" placeholder="" clearable></el-input>
                </el-form-item>
                <el-form-item label="" prop="desc">
                    <el-input v-model="updateProjectData.desc" placeholder="desc" clearable></el-input>
                </el-form-item>
                <el-form-item>
                    <el-button type="primary" @click="updateProjectSubmit('updateProjectForm')"></el-button>
                    <el-button @click="resetForm('updateProjectForm')"></el-button>
                </el-form-item>
            </el-form>
        </el-dialog>
    </section>
</template>
<script>
    import {
        getProjectList,  //
        findOne,  //id
        getLogoUrl,  //logo url
        updateProject,  //
        addProject  //
    } from '../api/api.js';

    export default {
        name: 'project_manage',

        data() {
            let validateType = (rule, value, callback) => {
                if (value<0 || value>2) {
                    return callback(new Error('0-2'));
                } else {
                    return callback(new Error(''));
                }

                callback();
            };

            return {
                /**
                 * common
                 */
                listLoading: false,  //lodding
                dialogVisible: false,  //
                sels: [],  //

                project_id: "",  //id

                //
                search_list_arg: {
                    desc: "",
                    title: "",
                    type: ""
                },

                //
                page_arg: {
                    pagesize: "10",  // 
                    currentPage: "1",  // 
                    total: 0,  // table :total
                },

                /**
                 * 
                 */
                project_info: [],  //

                /**
                 * 
                 */
                //
                addProjectData: {
                    type: "",
                    amount: "",
                    title: "",
                    desc: "",
                    logoUrl: ""
                },

                //
                upload_arg: {
                    limit:1,
                    logoFile: [],
                },

                //
                addProjectRules: {
                    type: [
                        { required: true, message: '', trigger: 'blur' },
                        { validator: validateType, trigger: 'blur' },
                    ],
                    amount: [
                        { required: true, message: '', trigger: 'blur' },
                    ],
                    title: [
                        { required: true, message: '', trigger: 'blur' },
                    ],
                    desc: [
                        { required: true, message: '', trigger: 'blur' },
                    ],
                },

                /**
                 *  
                 */
                //
                updateProjectData: {
                    id: "",
                    type: "",
                    amount: "",
                    title: "",
                    desc: "",
                    logoUrl: "",
                },
                //
                updateProjectRules: {
                    type: [
                        { required: true, message: '', trigger: 'blur' },
                        { validator: validateType, trigger: 'blur' },
                    ],
                    amount: [
                        { required: true, message: '', trigger: 'blur' },
                    ],
                    title: [
                        { required: true, message: '', trigger: 'blur' },
                    ],
                    desc: [
                        { required: true, message: '', trigger: 'blur' },
                    ],
                    logoUrl: [
                        { required: true, message: 'logo', trigger: 'blur' },
                    ]
                },

                /**
                 *  
                 */
                addProjectVisible: false,  //
                updateProjectVisible: false,  //
            }
        },
        methods: {
            /**
             * common
             */
            //
            handleClose(done) {
                this.$confirm('').then(() => {
                    done();
                }).catch(err => {
                    throw err;
                });
            },
            //
            resetForm(formName) {
                this.$refs[formName].resetFields();
            },
            //
            getFileType(fileName){
                let fileLength = fileName.length,  //
                    beforeFileLength = fileName.lastIndexOf('.');  //

                //
                let suffix = fileName.substring(beforeFileLength+1, fileLength)

                return suffix;
            },

            /**
             * el-upload common
             */
            // 
            handleExceed(files, fileList) {
                this.$message.warning(` ${this.upload_arg.limit}  ${files.length}  ${files.length + fileList.length} `);
            },
            // 
            beforeRemove(file) {
                this.addProjectData.logoUrl = "";
                this.updateProjectData.logoUrl = "";
                return this.$confirm(` ${ file.name }`);
            },

            /**
             *  
             */
            // --
            handleCurrentChange(val) {
                this.page_arg.currentPage = val;
                this.getProjectList();
            },
            // --
            handleSizeChange (size) {
                this.page_arg.pagesize = size;
                this.getProjectList();
            },

            /**
             * api
             */
            //
            getProjectList () {
                //
                let param = {
                    pageSize: this.page_arg.pagesize,
                    pageNum: this.page_arg.currentPage,
                };

                getProjectList(JSON.stringify(param)).then(res => {
                    this.page_arg.total = res.data.data.total;
                    this.project_info = res.data.data.list;
                }).catch({});
            },
            //
            findProject () {
                //
                let param = {
                    id: this.project_id
                };

                //
                findOne(param).then(res => {
                    this.$message({
                        message: "",
                        type: "success"
                    });

                    this.project_info = [];
                    this.project_info.push(res.data.data);
                }).catch({});
            },
            //
            findProjectList () {
                getProjectList(JSON.stringify(this.search_list_arg)).then(res => {
                    this.page_arg.total = res.data.data.total;
                    this.project_info = res.data.data.list;
                }).catch({});
            },

            /**
             * 
             */
            //el-upload
            // 
            addHandleChange(file){
                // console.log("");

                //pushfiles
                this.upload_arg.logoFile.push(file.raw);

                //
                this.addUpload();
            },

            // logo url
            addUpload(){
                let formData = new FormData();

                formData.append('logoFile', this.upload_arg.logoFile[0]);

                let config = {
                    headers: {
                        'Content-Type': 'multipart/form-data'
                    }
                };
                getLogoUrl(formData, config).then(res=>{
                    this.addProjectData.logoUrl = res.data.data.logoUrl;
                });
            },

            //
            addProjectSubmit (formName) {
                this.listLoading = true;  //loading

                //
                this.$refs[formName].validate((valid) => {
                    //
                    if (valid) {
                        addProject(qs.stringify(this.addProjectData)).then(() => {
                            this.$message({
                                message: "",
                                type: "success"
                            });

                            this.addProjectVisible = false;  //
                            this.listLoading = false;  //loading
                            this.getProjectList();  //
                        }).catch({});
                    } else {  //
                        console.log('error submit!!');
                        return false;
                    }
                });
            },

            /**
             * 
             */
            // 
            updateHandleChange(file){
                //pushfiles
                this.upload_arg.logoFile.push(file.raw);

                //
                this.updateUpload();
            },
            // logo url
            updateUpload(){
                let formData = new FormData();

                formData.append('logoFile', this.upload_arg.logoFile[0]);

                let config = {
                    headers: {
                        'Content-Type': 'multipart/form-data'
                    }
                };
                getLogoUrl(formData, config).then(res=>{
                    this.updateProjectData.logoUrl = res.data.data.logoUrl;
                })
            },
            //
            updateProject (row) {
                this.updateProjectData = Object.assign({}, row);
            },
            //
            updateProjectSubmit(formName) {
                this.listLoading = true;  //loading

                //
                this.$refs[formName].validate((valid) => {
                    //
                    if (valid) {
                        updateProject(this.updateProjectData).then(() => {
                            this.updateProjectVisible = false;  //

                            this.$message({
                                message: "",
                                type: "success"
                            });

                            this.listLoading = false;  //loading
                            this.getProjectList();  //
                        }).catch({});
                    } else {  //
                        console.log('error submit!!');
                        return false;
                    }
                });
            }
        },
        created () {
            this.getProjectList();
        }
    }
</script>

<style scoped>
    .tabimg{
        display: block;
        width: 40px;
        height: 40px;
        margin: 0 auto;
    }

    .tip{
        font-size: 12px;
        color: #717171;
    }

    .mgt{
        margin-bottom: 25px;
    }
</style>